import React, { useContext, useEffect, useState } from "react";
import ReactDOM from "react-dom/client";
import './style.css';

//  Importo los componentes de aframe que se usarán en toda la escena AFRAME
require('./components/aframe-components/follow-entity');
require('./components/aframe-components/load-glb-collision');
require('./components/aframe-components/Player/movement-controls');
require('./components/aframe-components/Personalization/character-textures');
require('./components/aframe-components/Player/load-character-prefs');
require('./components/aframe-components/ExperienceComponents/mall-materials');
require('./components/aframe-components/ExperienceComponents/mall-data');
require('./components/aframe-components/ExperienceComponents/local-teleporter');
require('./components/aframe-components/Player/naf-remote-avatar');
require('../modules/aframe-orbit-controls-component');
require('./components/aframe-components/aframe-look-at-component.min');
require('./components/aframe-components/adjust-image');
require('./components/aframe-components/ExperienceComponents/image-gallery')

//  Importación de componentes de React
import BottomMenu from "./components/react-components/experience/BottomMenu/BottomMenu";
import Header from "./components/react-components/Header/Header";
import Joystick from "./components/react-components/Joystick/Joystick";

//  Importación de contextos
import { UserDataContext, emptypreferenciasUsuario } from "./context/UserDataContext";
import { MultiplayerProvider } from "./context/MultiplayerContext";
import { UserDataProvider } from "./context/UserDataContext";
import { ContextTemplateProvider } from "./components/react-components/ContextTemplate";


//  Importo la libreria de axios
const axios = require('../modules/axios.min').default; 

const App = () => {

  // Contexto de los datos de usuario
  const { userData }  = useContext(UserDataContext);

  //  Estados
  // const [ idEmpresaUsuario, setIdEmpresaUsuario ] = useState("511095a8-da38-4acd-9254-90f82c75fb5b");
  const [ EventVRToken, setEventVRToken ] = useState('');

  useEffect(()=>{
    //  Verifico que no sea un estado sin datos aún consultados del locastorage en userdataconstext
    if(userData !== emptypreferenciasUsuario){
  
      //  verifico que existan datos de personalización del usuario
      if(userData?.preferenciasUsuario?.avatar != '' && 
        userData?.preferenciasUsuario?.ropa != {})
      {
        // console.log('userData verificada', userData)
        //  Obtengo la entidad del modelo del jugador y le easigno el componente para aplicar modelo y ropa
        const playerEntity = document.querySelector('#player-model');
        playerEntity.setAttribute('load-character-prefs', 
          { userPrefs: JSON.stringify(userData.preferenciasUsuario) });

        const nameTextEntity = document.querySelector('#player-text-name');
        if(nameTextEntity){
          nameTextEntity.setAttribute('text', { value: userData.preferenciasUsuario.nombre || 'SIN NOMBRE'});
        }

      }
    }
    
  }, [userData]);

  // //  Al iniciar la aplicación 
  useEffect(()=>{
    getDataEventVr();
  },[]);

  const getDataEventVr = () => {
    const bodyLogin = {
      "request_data": {
      "type_action": "login",
          "data": {
              "correo_usuario": process.env.EVENTVR_USER,
              "clave_usuario": process.env.EVENTVR_PASSWORD
          }
      },
      "headers": {
          "Content-Type": "application/json",
          "Accept": "application/json"
      }
    }

    // Axios
    axios.post(process.env.URL_AMAZON_ENDPOINTS + 'login', bodyLogin, {
        headers: {
            "Content-Type": "application/json",
            "Accept": "application/json",
        },
    })
    .then((response) => {
        if(response.status == '200' && response.data.status != 'failed'){
            setEventVRToken (response.data.result.token);
            getImages(response.data.result.token, )
            console.log('token', response.data.result.token);
        }
    })
    .catch(error => {
        console.error('error : ', error)
    });
  }

  const getImages = async(token) =>{

    // Consulto los datos de publicidad del marketplace
    const publicidadResponse = await axios.post(process.env.URL_AMAZON_ENDPOINTS + 'marketplaces', 
    {
      "request_data": {
        "type_action": "get_all_pu",
        "data": {
          "id_marketplace": "7ca954e4-98e2-4eb3-8f8c-3a29ca4f87a8"
        }
      },
      "headers": {
        "Content-Type": "application/json",
        "Accept": "application/json",
        "Authorization": token
      }
    }, 
    {
      headers: {
          "Content-Type": "application/json",
          "Accept": "application/json",
      },
    })

    //  Verifico que exista datos de publicidad
    if(publicidadResponse.status == '200' && publicidadResponse.data.status != 'failed'){
      // const publicidadEntity = document.querySelector('#publicidad-container');
      // if(publicidadEntity){
      //   //Agrego el componente encargado de setear las vallas publicitarias
      //   publicidadEntity.setAttribute('mall-data-publicity', {
      //     data_pu: JSON.stringify(publicidadResponse.data.body.data)
      //   })
      // }

      const arrayImages = publicidadResponse.data.body.data;

      if(arrayImages && arrayImages.length > 0){
        arrayImages.forEach(image => {

          //obtengo la entidad que corresponde a la imágen consultada
          const imageEntity = document.querySelector('#publi-' + image.clave_publicidad);
          // console.log('entity 1', imageEntity);
          if(imageEntity && image.imagen_publicidad && 
              image.imagen_publicidad!=="" &&
              image.imagen_publicidad!=="imagen_publicidad"){

            imageEntity.setAttribute('visible', 'true');
            // imageEntity.setAttribute('src', process.env.URL_AMAZON_FILES + image.imagen_publicidad);
            imageEntity.setAttribute('adjust-image', {
              srcImage: process.env.URL_AMAZON_FILES + image.imagen_publicidad,
            });

            const dataImage = JSON.parse(image.posicion_publicidad);

            if(dataImage){
              imageEntity.setAttribute('image-gallery', {
                normalScale: '.02 .02 .02',
                hoverScale:'.023 .023 .023',
                urlImage: process.env.URL_AMAZON_FILES + image.imagen_publicidad,
                name: dataImage.nombre,
                description: dataImage.descripcion
              });
            }
            
          }
        });
      }
      
    }

  }

  return (
    <>
      <Header/>
      <BottomMenu/>
      {/* Verifico si es un dispositivo móvil y grafico el botón de movimiento táctil */}
      {/* {AFRAME.utils.device.isMobile () && <ForwardButton/>} */}
      <Joystick/>
    </>
    
    // <h1>{JSON.stringify(userData.preferenciasUsuario.avatar)}</h1>
  );
}


/*  El componente principal de React se debe cargar una vez el documento y los script han cargado
    Esto se puede cambiar en WEBPACK
    new HtmlWebpackPlugin({...scriptLoading: 'blocking',}),
    Si estuviese scriptLoading: 'defer' esto no es necesario pero Aframe 1.3.0 requiere 'blocking'.
    'blocking' carga los chunks (scripts) antes de que se cargue el documento, 
    así se reconocen las etiquetas AFRAME
*/
document.addEventListener('DOMContentLoaded', activate);

function activate () {

  // console.log('document.getElementById("root")',document.getElementById("root"))
  const root = ReactDOM.createRoot(document.getElementById("root"));
  root.render(
    // <React.StrictMode>
      <ContextTemplateProvider>
        <UserDataProvider>
          <MultiplayerProvider>
            <App />
          </MultiplayerProvider>
        </UserDataProvider>
        
      </ContextTemplateProvider>
      
    // </React.StrictMode>
  );
};


