AFRAME.registerComponent('image-gallery', {
  schema: {
    normalScale: {string: 'string', default: '1 1 1'},
    hoverScale: {string: 'string', default: '1.1 1.1 1.1'},
    urlImage: {string: 'string', default: ''},
    name: {string: 'string', default: ''},
    description: {string: 'string', default: ''}
  },

  init: function() {
    const data = this.data;

    this.el.addEventListener('mouseenter', (e) => {
      this.el.setAttribute('scale', data.hoverScale);
    })
    this.el.addEventListener('mouseleave', (e) => {
      this.el.setAttribute('scale', data.normalScale);
    })

    this.el.addEventListener('click', (e) => {
      const OpenImageEvent = new CustomEvent('open_local_info', {
          detail: {
            urlImage: data.urlImage,
            name: data.name,
            description: data.description
          }
      });
      console.log('click')
      document.dispatchEvent(OpenImageEvent);
    })
  }
})